___CSS_LOADER_EXPORT___.push([module.id, `/* CSS Variables */
:root {
  --primary: #6366f1;
  --primary-dark: #4f46e5;
  --primary-light: #818cf8;
  --secondary: #ec4899;
  --success: #10b981;
  --warning: #f59e0b;
  --danger: #ef4444;
  --dark: #1e293b;
  --dark-light: #334155;
  --light: #f8fafc;
  --text-primary: #0f172a;
  --text-secondary: #64748b;
  --border: #e2e8f0;
  --shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    sans-serif;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: var(--text-primary);
  overflow-x: hidden;
  line-height: 1.6;
  min-height: 100vh;
}

/* App Wrapper - Simplified */
.app-wrapper {
  padding: 2rem;
  min-height: 100vh;
}

.container {
  display: grid;
  grid-template-columns: 320px 1fr;
  gap: 2rem;
  max-width: 1600px;
  margin: 0 auto;
  height: calc(100vh - 4rem);
}

/* Sidebar - Optimized */
.sidebar {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  padding: 2rem;
  overflow-y: auto;
  box-shadow: var(--shadow-lg);
  will-change: transform;
}

.sidebar-header {
  margin-bottom: 2.5rem;
}

.logo {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 0.5rem;
}

.logo-icon {
  width: 48px;
  height: 48px;
  background: linear-gradient(135deg, var(--primary), var(--secondary));
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  color: white;
  font-weight: bold;
  box-shadow: 0 4px 8px rgba(99, 102, 241, 0.3);
}

.sidebar h1 {
  font-size: 1.75rem;
  font-weight: 800;
  background: linear-gradient(135deg, var(--primary), var(--secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: -0.5px;
}

.tagline {
  color: var(--text-secondary);
  font-size: 0.875rem;
  font-weight: 400;
  margin-left: 64px;
}

.section-label {
  color: var(--text-secondary);
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 1rem;
}

.projects-list {
  margin-bottom: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.project-item {
  padding: 1rem;
  background: var(--light);
  border: 2px solid transparent;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: var(--text-primary);
}

.project-item:hover {
  border-color: var(--primary-light);
  background: white;
  transform: translateX(4px);
}

.project-item.active {
  background: linear-gradient(135deg, var(--primary), var(--secondary));
  color: white;
  box-shadow: 0 4px 12px rgba(99, 102, 241, 0.3);
}

.project-item span {
  flex: 1;
  font-weight: 500;
}

.delete-project {
  background: var(--danger);
  border: none;
  color: white;
  padding: 0.375rem 0.75rem;
  border-radius: 8px;
  cursor: pointer;
  font-size: 0.75rem;
  font-weight: 600;
  transition: all 0.2s ease;
}

.delete-project:hover {
  background: #dc2626;
}

/* Main Content - Optimized */
.main-content {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  overflow: hidden;
  box-shadow: var(--shadow-lg);
}

.content-wrapper {
  padding: 2.5rem;
  height: 100%;
  overflow-y: auto;
}

.todo-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2.5rem;
  padding-bottom: 1.5rem;
  border-bottom: 2px solid var(--border);
}

.header-left h2 {
  font-size: 2.5rem;
  font-weight: 800;
  color: var(--text-primary);
  margin-bottom: 0.25rem;
  letter-spacing: -1px;
  background: linear-gradient(135deg, var(--primary), var(--secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.header-subtitle {
  color: var(--text-secondary);
  font-size: 0.875rem;
  font-weight: 400;
}

/* Buttons - Simplified */
.btn {
  padding: 0.875rem 1.75rem;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  font-size: 0.9375rem;
  font-weight: 600;
  transition: all 0.2s ease;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  font-family: inherit;
}

.btn-icon {
  font-size: 1.25rem;
}

.btn-primary {
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  color: white;
  box-shadow: 0 4px 12px rgba(99, 102, 241, 0.3);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(99, 102, 241, 0.4);
}

.btn-primary:active {
  transform: translateY(0);
}

.btn-secondary {
  background: var(--light);
  color: var(--text-secondary);
  border: 1px solid var(--border);
}

.btn-secondary:hover {
  background: white;
  border-color: var(--primary);
  color: var(--primary);
}

.btn-floating {
  box-shadow: 0 6px 20px rgba(99, 102, 241, 0.4);
}

.btn-add-project {
  width: 100%;
  justify-content: center;
  background: var(--light);
  color: var(--primary);
  border: 2px dashed var(--border);
}

.btn-add-project:hover {
  background: white;
  border-color: var(--primary);
}

/* Todo Items - Performance Optimized */
.todos-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.todo-item {
  background: white;
  padding: 1.75rem;
  border-radius: 16px;
  box-shadow: var(--shadow);
  border-left: 4px solid var(--primary);
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.2s ease;
  will-change: transform;
}

.todo-item.priority-low {
  border-left-color: var(--success);
}

.todo-item.priority-medium {
  border-left-color: var(--warning);
}

.todo-item.priority-high {
  border-left-color: var(--danger);
}

.todo-item:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

.todo-item.completed {
  opacity: 0.6;
  background: var(--light);
}

.todo-item.completed .todo-title {
  text-decoration: line-through;
  color: var(--text-secondary);
}

.todo-info {
  flex: 1;
}

.todo-title {
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 0.75rem;
  color: var(--text-primary);
  line-height: 1.4;
}

.todo-meta {
  display: flex;
  gap: 1.5rem;
  font-size: 0.875rem;
  color: var(--text-secondary);
}

.todo-meta span {
  display: flex;
  align-items: center;
  gap: 0.375rem;
}

.todo-actions {
  display: flex;
  gap: 0.5rem;
}

.todo-actions button {
  padding: 0.625rem 1.125rem;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 0.875rem;
  font-weight: 600;
  transition: all 0.2s ease;
  font-family: inherit;
}

.view-btn {
  background: var(--primary);
  color: white;
}

.view-btn:hover {
  background: var(--primary-dark);
}

.complete-btn {
  background: var(--success);
  color: white;
}

.complete-btn:hover {
  background: #059669;
}

.delete-btn {
  background: var(--danger);
  color: white;
}

.delete-btn:hover {
  background: #dc2626;
}

/* Modal - Simplified */
.modal {
  display: none;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.modal.show {
  display: block;
}

.modal-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}

.modal-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  max-width: 600px;
}

.modal-content {
  background: white;
  border-radius: 20px;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);
  overflow: hidden;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 2rem 1.5rem;
  border-bottom: 1px solid var(--border);
}

.modal-header h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
}

.close {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.75rem;
  cursor: pointer;
  color: var(--text-secondary);
  border-radius: 10px;
  transition: all 0.2s ease;
  background: transparent;
}

.close:hover {
  background: var(--light);
  color: var(--text-primary);
}

.modal-content form {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--text-primary);
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.modal-content input,
.modal-content textarea,
.modal-content select {
  padding: 0.875rem 1rem;
  border: 2px solid var(--border);
  border-radius: 12px;
  font-size: 0.9375rem;
  font-family: inherit;
  transition: border-color 0.2s ease;
  background: white;
}

.modal-content input:focus,
.modal-content textarea:focus,
.modal-content select:focus {
  outline: none;
  border-color: var(--primary);
}

.modal-content textarea {
  resize: vertical;
  min-height: 80px;
}

.form-actions {
  display: flex;
  gap: 1rem;
  margin-top: 0.5rem;
}

.form-actions button {
  flex: 1;
}

.hidden {
  display: none !important;
}

/* Empty State */
.empty-state {
  text-align: center;
  padding: 4rem 2rem;
}

.empty-state p {
  font-size: 1.125rem;
  color: var(--text-secondary);
  margin-bottom: 1rem;
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: rgba(148, 163, 184, 0.3);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(148, 163, 184, 0.5);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .container {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .sidebar {
    height: auto;
  }

  .main-content {
    min-height: 500px;
  }
}

@media (max-width: 768px) {
  .app-wrapper {
    padding: 1rem;
  }

  .container {
    height: auto;
  }

  .todo-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }

  .todo-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }

  .todo-actions {
    width: 100%;
  }

  .todo-actions button {
    flex: 1;
  }

  .form-row {
    grid-template-columns: 1fr;
  }
}

/* Remove background animation elements if they exist */
.background-animation,
.gradient-orb {
  display: none;
}
