AAwB;EACxB,0BAA0B;AAC5B;;AAEA;EACE,aAAa;EACb,aAAa;EACb,sBAAsB;EACtB,WAAW;AACb;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,WAAW;AACb;;AAEA;EACE,mBAAmB;EACnB,gBAAgB;EAChB,0BAA0B;AAC5B;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,SAAS;AACX;;AAEA;;;EAGE,sBAAsB;EACtB,+BAA+B;EAC/B,mBAAmB;EACnB,oBAAoB;EACpB,oBAAoB;EACpB,kCAAkC;EAClC,iBAAiB;AACnB;;AAEA;;;EAGE,aAAa;EACb,4BAA4B;AAC9B;;AAEA;EACE,gBAAgB;EAChB,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,SAAS;EACT,kBAAkB;AACpB;;AAEA;EACE,OAAO;AACT;;AAEA;EACE,wBAAwB;AAC1B;;AAEA,gBAAgB;AAChB;EACE,kBAAkB;EAClB,kBAAkB;AACpB;;AAEA;EACE,mBAAmB;EACnB,4BAA4B;EAC5B,mBAAmB;AACrB;;AAEA,sBAAsB;AACtB;EACE,UAAU;EACV,WAAW;AACb;;AAEA;EACE,uBAAuB;AACzB;;AAEA;EACE,oCAAoC;EACpC,mBAAmB;AACrB;;AAEA;EACE,oCAAoC;AACtC;;AAEA,sBAAsB;AACtB;EACE;IACE,0BAA0B;IAC1B,WAAW;EACb;;EAEA;IACE,YAAY;EACd;;EAEA;IACE,iBAAiB;EACnB;AACF;;AAEA;EACE;IACE,aAAa;EACf;;EAEA;IACE,YAAY;EACd;;EAEA;IACE,sBAAsB;IACtB,uBAAuB;IACvB,SAAS;EACX;;EAEA;IACE,sBAAsB;IACtB,uBAAuB;IACvB,SAAS;EACX;;EAEA;IACE,WAAW;EACb;;EAEA;IACE,OAAO;EACT;;EAEA;IACE,0BAA0B;EAC5B;AACF;;AAEA,uDAAuD;AACvD;;EAEE,aAAa;AACf","sourcesContent":["/* CSS Variables */\r\n:root {\r\n  --primary: #6366f1;\r\n  --primary-dark: #4f46e5;\r\n  --primary-light: #818cf8;\r\n  --secondary: #ec4899;\r\n  --success: #10b981;\r\n  --warning: #f59e0b;\r\n  --danger: #ef4444;\r\n  --dark: #1e293b;\r\n  --dark-light: #334155;\r\n  --light: #f8fafc;\r\n  --text-primary: #0f172a;\r\n  --text-secondary: #64748b;\r\n  --border: #e2e8f0;\r\n  --shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),\r\n    0 2px 4px -1px rgba(0, 0, 0, 0.06);\r\n  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1),\r\n    0 4px 6px -2px rgba(0, 0, 0, 0.05);\r\n}\r\n\r\n* {\r\n  margin: 0;\r\n  padding: 0;\r\n  box-sizing: border-box;\r\n}\r\n\r\nbody {\r\n  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI',\r\n    sans-serif;\r\n  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);\r\n  color: var(--text-primary);\r\n  overflow-x: hidden;\r\n  line-height: 1.6;\r\n  min-height: 100vh;\r\n}\r\n\r\n/* App Wrapper - Simplified */\r\n.app-wrapper {\r\n  padding: 2rem;\r\n  min-height: 100vh;\r\n}\r\n\r\n.container {\r\n  display: grid;\r\n  grid-template-columns: 320px 1fr;\r\n  gap: 2rem;\r\n  max-width: 1600px;\r\n  margin: 0 auto;\r\n  height: calc(100vh - 4rem);\r\n}\r\n\r\n/* Sidebar - Optimized */\r\n.sidebar {\r\n  background: rgba(255, 255, 255, 0.95);\r\n  border-radius: 20px;\r\n  padding: 2rem;\r\n  overflow-y: auto;\r\n  box-shadow: var(--shadow-lg);\r\n  will-change: transform;\r\n}\r\n\r\n.sidebar-header {\r\n  margin-bottom: 2.5rem;\r\n}\r\n\r\n.logo {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 1rem;\r\n  margin-bottom: 0.5rem;\r\n}\r\n\r\n.logo-icon {\r\n  width: 48px;\r\n  height: 48px;\r\n  background: linear-gradient(135deg, var(--primary), var(--secondary));\r\n  border-radius: 12px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  font-size: 24px;\r\n  color: white;\r\n  font-weight: bold;\r\n  box-shadow: 0 4px 8px rgba(99, 102, 241, 0.3);\r\n}\r\n\r\n.sidebar h1 {\r\n  font-size: 1.75rem;\r\n  font-weight: 800;\r\n  background: linear-gradient(135deg, var(--primary), var(--secondary));\r\n  -webkit-background-clip: text;\r\n  -webkit-text-fill-color: transparent;\r\n  background-clip: text;\r\n  letter-spacing: -0.5px;\r\n}\r\n\r\n.tagline {\r\n  color: var(--text-secondary);\r\n  font-size: 0.875rem;\r\n  font-weight: 400;\r\n  margin-left: 64px;\r\n}\r\n\r\n.section-label {\r\n  color: var(--text-secondary);\r\n  font-size: 0.75rem;\r\n  font-weight: 600;\r\n  text-transform: uppercase;\r\n  letter-spacing: 1px;\r\n  margin-bottom: 1rem;\r\n}\r\n\r\n.projects-list {\r\n  margin-bottom: 1.5rem;\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 0.5rem;\r\n}\r\n\r\n.project-item {\r\n  padding: 1rem;\r\n  background: var(--light);\r\n  border: 2px solid transparent;\r\n  border-radius: 12px;\r\n  cursor: pointer;\r\n  transition: all 0.2s ease;\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  color: var(--text-primary);\r\n}\r\n\r\n.project-item:hover {\r\n  border-color: var(--primary-light);\r\n  background: white;\r\n  transform: translateX(4px);\r\n}\r\n\r\n.project-item.active {\r\n  background: linear-gradient(135deg, var(--primary), var(--secondary));\r\n  color: white;\r\n  box-shadow: 0 4px 12px rgba(99, 102, 241, 0.3);\r\n}\r\n\r\n.project-item span {\r\n  flex: 1;\r\n  font-weight: 500;\r\n}\r\n\r\n.delete-project {\r\n  background: var(--danger);\r\n  border: none;\r\n  color: white;\r\n  padding: 0.375rem 0.75rem;\r\n  border-radius: 8px;\r\n  cursor: pointer;\r\n  font-size: 0.75rem;\r\n  font-weight: 600;\r\n  transition: all 0.2s ease;\r\n}\r\n\r\n.delete-project:hover {\r\n  background: #dc2626;\r\n}\r\n\r\n/* Main Content - Optimized */\r\n.main-content {\r\n  background: rgba(255, 255, 255, 0.95);\r\n  border-radius: 20px;\r\n  overflow: hidden;\r\n  box-shadow: var(--shadow-lg);\r\n}\r\n\r\n.content-wrapper {\r\n  padding: 2.5rem;\r\n  height: 100%;\r\n  overflow-y: auto;\r\n}\r\n\r\n.todo-header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  margin-bottom: 2.5rem;\r\n  padding-bottom: 1.5rem;\r\n  border-bottom: 2px solid var(--border);\r\n}\r\n\r\n.header-left h2 {\r\n  font-size: 2.5rem;\r\n  font-weight: 800;\r\n  color: var(--text-primary);\r\n  margin-bottom: 0.25rem;\r\n  letter-spacing: -1px;\r\n  background: linear-gradient(135deg, var(--primary), var(--secondary));\r\n  -webkit-background-clip: text;\r\n  -webkit-text-fill-color: transparent;\r\n  background-clip: text;\r\n}\r\n\r\n.header-subtitle {\r\n  color: var(--text-secondary);\r\n  font-size: 0.875rem;\r\n  font-weight: 400;\r\n}\r\n\r\n/* Buttons - Simplified */\r\n.btn {\r\n  padding: 0.875rem 1.75rem;\r\n  border: none;\r\n  border-radius: 12px;\r\n  cursor: pointer;\r\n  font-size: 0.9375rem;\r\n  font-weight: 600;\r\n  transition: all 0.2s ease;\r\n  display: inline-flex;\r\n  align-items: center;\r\n  gap: 0.5rem;\r\n  font-family: inherit;\r\n}\r\n\r\n.btn-icon {\r\n  font-size: 1.25rem;\r\n}\r\n\r\n.btn-primary {\r\n  background: linear-gradient(135deg, var(--primary), var(--primary-dark));\r\n  color: white;\r\n  box-shadow: 0 4px 12px rgba(99, 102, 241, 0.3);\r\n}\r\n\r\n.btn-primary:hover {\r\n  transform: translateY(-2px);\r\n  box-shadow: 0 6px 16px rgba(99, 102, 241, 0.4);\r\n}\r\n\r\n.btn-primary:active {\r\n  transform: translateY(0);\r\n}\r\n\r\n.btn-secondary {\r\n  background: var(--light);\r\n  color: var(--text-secondary);\r\n  border: 1px solid var(--border);\r\n}\r\n\r\n.btn-secondary:hover {\r\n  background: white;\r\n  border-color: var(--primary);\r\n  color: var(--primary);\r\n}\r\n\r\n.btn-floating {\r\n  box-shadow: 0 6px 20px rgba(99, 102, 241, 0.4);\r\n}\r\n\r\n.btn-add-project {\r\n  width: 100%;\r\n  justify-content: center;\r\n  background: var(--light);\r\n  color: var(--primary);\r\n  border: 2px dashed var(--border);\r\n}\r\n\r\n.btn-add-project:hover {\r\n  background: white;\r\n  border-color: var(--primary);\r\n}\r\n\r\n/* Todo Items - Performance Optimized */\r\n.todos-list {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 1rem;\r\n}\r\n\r\n.todo-item {\r\n  background: white;\r\n  padding: 1.75rem;\r\n  border-radius: 16px;\r\n  box-shadow: var(--shadow);\r\n  border-left: 4px solid var(--primary);\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  transition: all 0.2s ease;\r\n  will-change: transform;\r\n}\r\n\r\n.todo-item.priority-low {\r\n  border-left-color: var(--success);\r\n}\r\n\r\n.todo-item.priority-medium {\r\n  border-left-color: var(--warning);\r\n}\r\n\r\n.todo-item.priority-high {\r\n  border-left-color: var(--danger);\r\n}\r\n\r\n.todo-item:hover {\r\n  transform: translateY(-2px);\r\n  box-shadow: var(--shadow-lg);\r\n}\r\n\r\n.todo-item.completed {\r\n  opacity: 0.6;\r\n  background: var(--light);\r\n}\r\n\r\n.todo-item.completed .todo-title {\r\n  text-decoration: line-through;\r\n  color: var(--text-secondary);\r\n}\r\n\r\n.todo-info {\r\n  flex: 1;\r\n}\r\n\r\n.todo-title {\r\n  font-size: 1.125rem;\r\n  font-weight: 600;\r\n  margin-bottom: 0.75rem;\r\n  color: var(--text-primary);\r\n  line-height: 1.4;\r\n}\r\n\r\n.todo-meta {\r\n  display: flex;\r\n  gap: 1.5rem;\r\n  font-size: 0.875rem;\r\n  color: var(--text-secondary);\r\n}\r\n\r\n.todo-meta span {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 0.375rem;\r\n}\r\n\r\n.todo-actions {\r\n  display: flex;\r\n  gap: 0.5rem;\r\n}\r\n\r\n.todo-actions button {\r\n  padding: 0.625rem 1.125rem;\r\n  border: none;\r\n  border-radius: 10px;\r\n  cursor: pointer;\r\n  font-size: 0.875rem;\r\n  font-weight: 600;\r\n  transition: all 0.2s ease;\r\n  font-family: inherit;\r\n}\r\n\r\n.view-btn {\r\n  background: var(--primary);\r\n  color: white;\r\n}\r\n\r\n.view-btn:hover {\r\n  background: var(--primary-dark);\r\n}\r\n\r\n.complete-btn {\r\n  background: var(--success);\r\n  color: white;\r\n}\r\n\r\n.complete-btn:hover {\r\n  background: #059669;\r\n}\r\n\r\n.delete-btn {\r\n  background: var(--danger);\r\n  color: white;\r\n}\r\n\r\n.delete-btn:hover {\r\n  background: #dc2626;\r\n}\r\n\r\n/* Modal - Simplified */\r\n.modal {\r\n  display: none;\r\n  position: fixed;\r\n  z-index: 1000;\r\n  left: 0;\r\n  top: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n}\r\n\r\n.modal.show {\r\n  display: block;\r\n}\r\n\r\n.modal-backdrop {\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  background: rgba(0, 0, 0, 0.5);\r\n}\r\n\r\n.modal-container {\r\n  position: absolute;\r\n  top: 50%;\r\n  left: 50%;\r\n  transform: translate(-50%, -50%);\r\n  width: 90%;\r\n  max-width: 600px;\r\n}\r\n\r\n.modal-content {\r\n  background: white;\r\n  border-radius: 20px;\r\n  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1),\r\n    0 10px 10px -5px rgba(0, 0, 0, 0.04);\r\n  overflow: hidden;\r\n